import React, {
	ElementType, forwardRef, HTMLAttributes,
} from "react";

import { displayFontSizes } from "../styleguide";

import { StyledTypographyProps, Text } from "./Typography.styles";

type TypographyBaseAttributes = HTMLAttributes<HTMLElement>;

export interface TypographyProps extends TypographyBaseAttributes, StyledTypographyProps {
	dataQA: string,
	variant?: keyof typeof displayFontSizes
	/**
	 * @description The component used for the root node.
	 * */
	component?: ElementType
}

const Typography = forwardRef<HTMLElement, TypographyProps>((props, ref) => {
	let typographyProps: TypographyProps;

	// if a variant is passed in, get the variant properties from displayFontSizes
	// also attach any other props that may have been passed in to override any default properties
	if (props.variant) {
		typographyProps = {
			...displayFontSizes[props.variant],
			...props,
		};
	} else {
		typographyProps = props;
	}

	return (
		<Text
			// https://styled-components.com/docs/api#as-polymorphic-prop
			as={props.component}
			data-qa={props.dataQA}
			ref={ref}
			{...typographyProps}
		>
			{props.children}
		</Text>
	);
});

Typography.displayName = "Typography";

export default Typography;
